import { useDispatch, useSelector } from 'react-redux'
import { addItem } from '../actions/cartActions'

export default function Products() {
    const items = useSelector(state=>state.cart.items)
    const dispatch = useDispatch()
    return (
        <div className="container">
            <h3 className="center">Our items</h3>
            <div className="d-flex justify-content-between flex-wrap">
                {items.map((item,i)=>
                <div className="card" key={i}>
                    <div className="card-image">
                        <img src={item.images[0]} width='100%' alt={item.productID}/>
                        <span className="card-title">{item.productID}</span>
                    </div>
                    <div className="card-content">
                        <p>{item.description}</p>
                        <p><b>Price: {item.price}$</b></p>
                        <p><input type='button' value='Add to Cart' onClick={()=> dispatch(addItem(item.productID))} className='btn btn-outline-success'/></p>
                    </div>
                </div> 
                )}
            </div>
        </div>
    )
}