// import { motion } from "framer-motion";
import { NavLink } from 'react-router-dom';
// import { useSelector } from 'react-redux'
import { useState } from 'react';
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';
import ShoppingCartOutlinedIcon from '@material-ui/icons/ShoppingCartOutlined';
import { destroySession } from '../actions/userActions'
import { useSelector, useDispatch } from 'react-redux'
import { faFacebook, faInstagram } from "@fortawesome/free-brands-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Subscribe from './Subscribe'

export default function Navbar() {
    const { items, user } = useSelector(state=>({
        items: state.cart.addedItems,
        user: state.currentUser
    }))
    const [ isActive, setActive ] = useState(false)
    const toggleClass = (e) => { e.preventDefault(); setActive(!isActive) }
    const dispatch = useDispatch()
    const [showModal, setShowModal] = useState(false);
    const handleModal = (e)=> setShowModal(e)

    return(
        <nav className='topNavbar'
        // initial={{ y: '-100vh' }} animate={{ y: 0 }} transition={{ delay: 1.5 }}
        >   <div className='nav-main-menu'>
                <NavLink to='/'>
                    <img className='logo' src={process.env.PUBLIC_URL + '/images/kavaglam_logo_long.svg'} alt="Kavaglam Logo" />
                </NavLink>
                <ul>
                    <li>{user.admin ? (<NavLink to='/admin'>ADMIN</NavLink>) : (<NavLink to='/'>HOME</NavLink>) }</li>
                    <li><NavLink to='/products'>BRUSHES</NavLink></li>
                    <li><NavLink to='/' className='collapsible'>OTHER</NavLink></li>
                    <li><NavLink to='/'>REVIEWS</NavLink></li>
                    <li><NavLink to='/'>FAQ</NavLink></li>
                    <li><NavLink to='/'>ABOUT</NavLink></li>
                    <li>
                        <NavLink to='/cart'>
                            <IconButton aria-label="show cart" color="inherit">
                                <Badge badgeContent={items ? items.reduce((acc,i)=>{return acc+i.quantity}, 0) : 0 } color="secondary" />
                                <ShoppingCartOutlinedIcon />
                            </IconButton>
                        </NavLink>
                    </li>
                    <li className="hamburger" onClick={toggleClass}>
                        <span /><span /> <span />
                    </li>
                </ul>
            </div>

            <div className={isActive ? 'overlay' : 'hide'} onClick={ toggleClass } />
            <Subscribe showModal={showModal} handleModal={handleModal} />

            <div className={isActive ? 'nav-side-menu' : 'hide'}>
                <a href='void(0)' onClick={ toggleClass }> <i className='icon-close'>X</i> </a>
                <div className='nav-side-menu-content'>
                    <img className='logo' src={process.env.PUBLIC_URL + '/images/kavaglam_logo_long.svg'} alt="Kavaglam Logo" />
                    <ul>
                        <li>{user.admin ? (<NavLink to='/admin'>ADMIN</NavLink>) : (<NavLink to='/'>HOME</NavLink>) }</li>
                        <li><NavLink to='/products'>BRUSHES</NavLink></li>
                        <li><NavLink to='/' className='collapsible'>OTHER</NavLink></li>
                        <li><NavLink to='/'>REVIEWS</NavLink></li>
                        <li><NavLink to='/'>FAQ</NavLink></li>
                        <li><NavLink to='/'>ABOUT</NavLink></li>
                        <li>
                            <NavLink to='/cart'>
                                <IconButton aria-label="show cart" color="inherit">
                                    <Badge badgeContent={items ? items.reduce((acc,i)=>{return acc+i.quantity}, 0) : 0 } color="secondary" />
                                    <ShoppingCartOutlinedIcon />
                                </IconButton>
                            </NavLink>
                        </li>
                        <li>{ user ? user.email : '' }</li>
                        <li><NavLink to='/sign_in'>sign in</NavLink></li>
                        <li><NavLink to='/sign_up'>register</NavLink></li>
                        <li><a href='void(0)' onClick={(e)=>{ e.preventDefault(); dispatch(destroySession()) }}>signout</a></li>
                        <li><a href='void(0)' onClick={(e)=>{ e.preventDefault(); handleModal(!showModal) }}>subscribe</a></li>
                        <li><a href='https://www.facebook.com/kavaglam/' ><FontAwesomeIcon icon={faFacebook}/></a></li>
                        <li><a href='https://www.instagram.com/kavaglam_/' ><FontAwesomeIcon icon={faInstagram}/></a></li>
                    </ul>
                </div>
            </div>
        </nav>
    )
}


