import { useState } from 'react'
import { subscribe } from '../actions/newsletterActions'

export default function Newsletter({ history }) {
    const [email, setEmail] = useState('')
    const handleInput = ({ value }) => setEmail(value)
    const handleFormSubmit = e => {
        e.preventDefault()
		email && subscribe({ email })
        history.push("/")
    }
    return (
        <>
            <form onSubmit={handleFormSubmit}>
                <input type='email' name='email' placeholder='Email' value={email} onChange={e=>handleInput(e.currentTarget)}/>
                <input type='submit' value='Submit' className='btn btn-primary' />
            </form>
        </>
    )
}