import { useState } from "react";
import { useSelector } from 'react-redux'
import { paymentIntent } from '../actions/payActions'
import { CardElement, useStripe, useElements } from "@stripe/react-stripe-js";

export default function CheckoutForm({ history }) {
    const { total, user } = useSelector(state=> ({
      total: state.cart.total,
      user: state.currentUser
    }))
    const [isProcessing, setProcessingTo] = useState(false);
    const [checkoutError, setCheckoutError] = useState();
    const stripe = useStripe();
    const elements = useElements();
    const handleCardDetailsChange = e => e.error ? setCheckoutError(e.error.message) : setCheckoutError()
    const handleFormSubmit = async e => {
        e.preventDefault()
        const billingDetails = {
          name: e.target.name.value,
          email: user.email,
          address: {
              city: user.address.locality,
              line1: `${user.address.subpremise} - ${user.address.street_number} ${user.address.route}`,
              state: user.address.administrative_area_level_1,
              postal_code: user.address.postal_code
          }
        };
        setProcessingTo(true);
        const cardElement = elements.getElement("card")
        try {
          const paymentInt = await paymentIntent({ amount: total*100 })
          const paymentMethodReq = await stripe.createPaymentMethod({
              type: "card",
              card: cardElement,
              billing_details: billingDetails
          })
          if (paymentMethodReq.error) {
            setCheckoutError(paymentMethodReq.error.message);
            setProcessingTo(false);
            return
          }
          const { error } = await stripe.confirmCardPayment(paymentInt.client_secret, {
            payment_method: paymentMethodReq.paymentMethod.id
          })
          if (error) {
            setCheckoutError(error.message);
            setProcessingTo(false);
            return
          }
          history.push("/success")
          } catch (err) {
            setCheckoutError("Payment failed!");
          }
    }
    const iframeStyles = {
      base: {
        color: "#000",
        fontSize: "16px",
        iconColor: "#000",
        "::placeholder": {
          color: "#87bbfd"
        }
      },
      invalid: {
        iconColor: "#FFC7EE",
        color: "#FFC7EE"
      },
      complete: {
        iconColor: "#cbf4c9"
      }
    }
    const cardElementOpts = {
      iconStyle: "solid",
      style: iframeStyles,
      hidePostalCode: true
    }

    return (
      <div className='container'>
      <form onSubmit={handleFormSubmit}>
          <input type="text"  name='name' placeholder="Full name on card" required />
          <CardElement options={cardElementOpts}  onChange={handleCardDetailsChange} />
          {checkoutError && (<p>{checkoutError}</p>) }
          <input type='submit' className='btn btn-success' disabled={isProcessing || !stripe} value={isProcessing ? "Processing..." : `Pay $${total}`} />
      </form>
      </div>
    );
};

