import GoogleAutoComplete from 'react-auto-complete-address-fields';
import { useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { createProfile } from '../actions/userActions'

export default function InfoForm({ history }) {
	const dispatch = useDispatch()
	const user = useSelector(state=>state.currentUser)
	const [email, setEmail] = useState('')
	const [address, setAddress] = useState({ subpremise:'', street_number:'', route:'', locality:'', administrative_area_level_1:'', postal_code:'', country:'' })
	const placeholder = ['Apartment, Unit, Suite, or Floor #','Steet Number*','Street Name*','City*','State/Province*','Postal Code*','Country/Region*']
	const handleInput = ({ name, value }) => name!=='email' ? setAddress(prev=>({...prev, [name]: value})) : setEmail(value) 
    const callbackFunc = autoCompleteData=> {
		autoCompleteData.address_components.forEach(item=>
			handleInput({ name: [item.types[0]], value: item.long_name })
		)
	}
	const handleFormSubmit = e => {
        e.preventDefault()
		user && dispatch(createProfile({email: user ? user.email : email, address: address}))
        history.push("/checkout")
    }
	
    return (
        <div className='container'>
			<h3>Contact Information</h3>
			<input type='text' name='email' value={user && user.email} placeholder='Email' onChange={e=>handleInput(e.currentTarget)} disabled={user && user.email} required />
			<h3>Enter your shipping address</h3>
			<GoogleAutoComplete
		      apiKey={process.env.REACT_APP_GOOGLE_KEY}
		      id = 'location'
		      fields= {{
				subpremise: "subpremise",
		        streetAddress: "route",
		        locality: "locality",
		        cityOrState: "administrative_area_level_1",
		        postalcode: "postal_code",
		        country: "country"
		      }}
		      callbackFunction={callbackFunc}
		    />
			<form onSubmit={handleFormSubmit}>
			{Object.keys(address).map((k,i)=>k!=='administrative_area_level_2' ? <input key={i} type='text' name={k} value={address[k]} placeholder={placeholder[i]} onChange={e=>handleInput(e.currentTarget)} required={k!=='subpremise'} /> : null)}
			<input type='submit' className='btn btn-success' value='Next' />
			</form>
		</div>
    )
}