import { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { NavLink } from 'react-router-dom'
import { removeItem, addQuantity, subtractQuantity, addShipping, subShipping, addPromo } from '../actions/cartActions'

export default function Cart(){
    const { total, items, addedItems } = useSelector(state => ({
        addedItems: state.cart.addedItems,
        items: state.cart.items,
        total: state.cart.total,
    }))
    const dispatch = useDispatch()
    const [promoMessage, setPromoMessage] = useState('');
    const handleChecked = e=> e.target.checked ?  dispatch(addShipping()) : dispatch(subShipping())
    const handlePromoSubmit = async (e) => {
        e.preventDefault()
        let promo_result = await dispatch(addPromo(e.target.promo_code.value.toUpperCase()))
        promo_result.data.error ? setPromoMessage('Invalid promo code!') : setPromoMessage(`Promo code ${e.target.promo_code.value.toUpperCase()} added`) 
    }
    return (
        <div className="container">
            <div className="cart">
                <h5>You have ordered:</h5>
                <ul className="collection">
                    {addedItems && addedItems.length ?
                    addedItems.map((item, i)=>
                        <li className="collection-item avatar" key={i}>
                            <div className="item-img">
                                <img src={items.find(i=>i.productID===item.product_id).images[0]} alt={item.product_id} className=""/>
                            </div>
                            <div className="item-desc">
                                <span className="title">{item.product_id}</span>
                                <p>{item.description}</p>
                                <p><b>Price: ${items.find(i=>i.productID===item.product_id).price} CAD</b></p> 
                                <p><b>Quantity: {item.quantity}</b> </p>
                                <div className="add-remove">
                                    <i className="material-icons" onClick={()=>dispatch(addQuantity(item))}> + </i>
                                    <i className="material-icons" onClick={()=>dispatch(subtractQuantity(item))}> - </i>
                                </div>
                                <button className="waves-effect waves-light btn pink remove" onClick={()=>dispatch(removeItem(item))}>Remove</button>
                            </div>
                        </li> 
                    ) : <p>Nothing.</p>}
                </ul>
            </div> 
            <div className="container mb-5">
                <div className="collection">
                    <li className="collection-item">
                        <label>
                            <input type="checkbox" onChange={handleChecked} />
                            <span>&nbsp;Shipping(+FREE SHIPPING$)</span>
                        </label>
                    </li>
                    <li className="collection-item">
                        <label>
                            <form onSubmit={handlePromoSubmit}>
                            <span>Promo code</span>
                            <input type="text" name='promo_code' />
                            <input type='submit' value='Submit' className='btn btn-secondary btn-sm' />
                            </form>
                            {promoMessage && (<p>{promoMessage}</p>) }
                        </label>
                    </li>
                    <li className="collection-item">
                        <b>Total: {total} $CAD</b>
                    </li>
                </div>
                <div className="checkout">
                    <NavLink to='/info'><button className="btn btn-outline-primary">Checkout</button></NavLink>
                </div>
            </div>        
        </div>
    )
}
